























import Vue from "vue";
export default Vue.extend({
  name: "upload",
  data() {
    return {
      fileList: []
    };
  },
  methods: {
    handleChange(file: string) {
      this.$emit("change", file);
    },
    handleRemove(file: File, fileList: File[]) {
      this.$emit("change", fileList);
    },
    handleExceed(files: File[], fileList: File[]) {
      this.$message.warning(
        ` 10  ${
          files.length
        }  ${files.length + fileList.length} `
      );
    },
    beforeRemove(file: File) {
      return this.$confirm(` ${file.name}`);
    }
  }
});
